.NavBar-container {
    padding: var(--s) var(--s);
    position: fixed;
    top: 0;
    background-color: white;
    width: 100%;
    height: 50px;
  }

  .NavBar-container li {
    display: inline-block;
    padding-right: 20px;
    cursor: pointer;
  }
  
  .NavBar-logo {
    color: var(--navy);
    font-size: 35px;
    position: fixed;
    left: 10px;
  }

  .new {
      opacity: 0;
      color: var(--navy);
      font-size: 35px;
  }

  .old, .new {
      transition: opacity 0.5s ease-in-out;
  }

  .wrap:hover .old {
      opacity: 0;
  }
  .wrap:hover .new {
      opacity: 1;
  }
  
  .NavBar-linkContainer {
    position: fixed;
    right: 10px;
    top: 20px;
    text-align: bottom;
  }

  
  
  .NavBar-link:visited {
    color: var(--navy);
    opacity: 0.6;
    text-decoration: none;
  }
  
  .NavBar-link:hover {
    opacity: 0.8;
    text-decoration: none;
  }

  .NavBar-link-active {
    opacity: 1.4;
    font-weight: bold;
  }


@media only screen and (max-width: 800px) {
  .NavBar-container {
    height: 30px;
  }
  .new {
    opacity: 0;
    color: var(--navy);
    font-size: 35px;
  }

  .old, .new {
      transition: opacity 0.5s ease-in-out;
  }

  .wrap:hover .old {
      opacity: 1;
  }
  .wrap:hover .new {
      opacity: 0;
  }

